import React from "react";
import { Button, Form, Modal } from "react-bootstrap";
import styles from "./styles.module.scss";

import users from "~/mocks/users";

function ModalInvite({ open, handleClose }) {
  return (
    <Modal show={open} onHide={handleClose} centered>
      <Modal.Header closeButton>
        <Modal.Title>Mời tham gia nhóm chat</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form>
          <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
            <Form.Label>Email người dùng</Form.Label>
            <Form.Control type="text" placeholder="Nhập email" />
          </Form.Group>

          <div className={styles["users-found"]}>
            {users.map((user) => (
              <div className={styles["user"]} key={user.id}>
                <div className={styles["info"]}>
                  <img src={user.imageURL} />
                  <span className={styles["name"]}>{user.displayName}</span>
                </div>
                <Button size="sm">Mời</Button>
              </div>
            ))}
          </div>
        </Form>
      </Modal.Body>
      <Modal.Footer>
        <Button variant="secondary" onClick={handleClose}>
          Đóng
        </Button>
      </Modal.Footer>
    </Modal>
  );
}

export default ModalInvite;
